/**=====================
55. theme layout CSS Start
==========================**/

body {
	&.dark-only {
		color: $dark-all-font-color;
		background-color: $dark-body-background;
		.apexcharts-tooltip.light {
			.apexcharts-tooltip-title {
				background: $dark-card-background;
				color: $white;
			}
		}
		.card {
			background-color: $dark-card-background;
		}
		.ecommerce-widget {
			border: 1px solid $dark-card-border !important;
		}
		.btn-light {
			color: $dark-all-font-color !important;
		}
		.form-select {
			color: $dark-all-font-color;
		}
		.add-project {
			.text-inherit {
				color: $dark-all-font-color;
			}
		}
		.knowledgebase-search {
			color: $theme-body-font-color;
			.form-control-plaintext {
				color: $theme-body-font-color;
			}
		}
		.btn-close {
			filter: brightness(0.8) invert(1);
		}
		.prooduct-details-box {
			.media {
				border: 1px solid $dark-card-border;
			}
		}
		.product-price {
			del {
				color: $dark-small-font-color;
			}
		}
		.ProfileCard {
			border: 1px solid $dark-card-border !important;
		}
		.form-control-plaintext {
			color: $dark-all-font-color;
		}
		.form-select {
			background-color: $dark-body-background;
			border: 1px solid $dark-border-color;
		}
		.box-layout {
			background-color: $dark-body-background;
			
			.page-wrapper,
			&.page-wrapper {
				.page-header {
					.header-wrapper {
						border: 1px solid $dark-border-color;
					}
				}
			}
		}
		.form-check-input {
			background-color: $dark-body-background;
		}
		.switch-state {
			&:before {
				background-color: $dark-card-background;
			}
		}
		// daterangepicker css start
		.daterangepicker {
			&:before {
				border-bottom-color: $dark-card-background;
			}
			.ranges {
				li {
					background-color: $dark-body-background;
					border-color: $dark-card-background;
					color: $dark-gray;
				}
			}
			.daterangepicker_input {
				.form-control {
					background-color: $dark-body-background;
					border-color: $dark-card-background !important;
				}
			}
			.calendar-time {
				select {
					border-color: $dark-card-background;
					background-color: $dark-body-background;
				}
			}
			&.dropdown-menu {
				background-color: $dark-card-background;
				-webkit-box-shadow: 0 0 14px $dark-body-background;
				box-shadow: 0 0 14px $dark-body-background;
			}
			.calendar-table {
				background-color: $dark-card-background;
				border-color: $dark-card-background;
			}
			&.ltr {
				td {
					&.in-range,&.off {
						background-color: $dark-body-background;
						color: $white;
					}
				}
			}
		}
		// daterangepicker css end
		// tooltip css start
		// tooltip css start
		.tooltip {
			&.bs-tooltip-top {
				.tooltip-arrow {
					&:before {
						border-top-color:$dark-card-inbox;
					}
				}
			}
			&.bs-tooltip-bottom {
				.tooltip-arrow {
					&:before {
						border-bottom-color: $dark-card-inbox;
						border-top-color: $transparent-color;
					}
				}
			}
			&.bs-tooltip-start {
				.tooltip-arrow {
					&:before {
						border-left-color: $dark-card-inbox;
						border-top-color: $transparent-color;
					}
				}
			}
			&.bs-tooltip-end {
				.tooltip-arrow {
					&:before {
						border-right-color: $dark-card-inbox;
						border-top-color: $transparent-color;
					}
				}
			}
			.tooltip-inner {
				background-color: $dark-card-inbox;
				color: $white;
			}
			.tooltip-arrow {
				&:before {
					border-top-color: $dark-card-inbox;
				}
			}
		}
		
		// tooltip css end
		
		.page-wrapper {
			&.only-body {
				.page-body-wrapper {
					.page-title {
						>.row {
							.col-6 {
								&:first-child {
									h6 {
										color: rgba($dark-all-font-color, 0.5);
									}
								}
							}
						}
					}
				}
				.page-header {
					.header-wrapper {
						.search-full {
							.form-group {
								.Typeahead {
									.u-posRelative {
										background-color: $dark-card-background;
									}
								}
							}
							
						}
					}
				}
			}
			
			.page-header {
				.header-wrapper {
					.nav-right {
						.profile-dropdown li span {
							color: $white;
						}
						
						.flip-card .flip-card-inner {
							.back {
								background-color: $dark-card-background !important;
								.flip-back-content {
									input {
										background-color: $dark-body-background;
										border: 1px solid $dark-card-border;
										color: $dark-all-font-color;
									}
								}
								li {
									&:last-child {
										border-top: 1px solid $dark-card-border;
									}
								}
							}
							
							.front {
								background-color: $dark-body-background !important;
								
								svg {
									stroke: $white;
								}
							}
						}
						
						.onhover-show-div:not(.profile-dropdown) {
							li {
								&:first-child {
									background-color: $dark-card-background;
								}
							}
						}
					}
				}
			}
		}
		
		
		.customizer-contain {
			color: $dark-body-background;
			
			.customizer-body {
				.main-layout {
					.box-layout {
						background-color: $white;
					}
				}
			}
		}
		
		.login-card {
			.btn-showcase .btn {
				background-color: $dark-card-background !important;
				color: #fff;
				border-color: $dark-card-background !important;
			}
			
			.login-main {
				box-shadow: 0 0 37px rgba($white, 0.05);
				
				.theme-form {
					input {
						background-color: $dark-card-background !important;
					}
					
					.or {
						&:before {
							
							background-color: $dark-card-background;
							
						}
					}
					
					.checkbox label::before {
						background-color: $dark-card-background !important;
						border-color: $dark-card-background !important;
						;
					}
				}
				
			}
		}
		
		.H_ui {
			~div {
				background-color: $dark-body-background !important;
			}
		}
		
		.swal-icon--success:after,
		.swal-icon--success:before {
			background: $dark-card-background;
		}
		
		.swal-icon--success__hide-corners {
			background-color: $dark-card-background;
		}
		
		.note-editor.note-frame {
			.note-status-output {
				border-top: 1px solid $dark-card-background;
			}
			
			.note-statusbar {
				border-top: 1px solid $dark-card-background;
				
				.note-resizebar {
					background-color: $dark-card-background;
				}
			}
		}
		
		.light-font {
			color: rgba(255, 255, 255, 70%);
		}
		
		.page-link {
			background-color: $dark-body-background;
			border: 1px solid $dark-card-border;
		}
		
		.b-r-light {
			border-right: 1px solid $dark-card-border !important;
		}
		
		.history-details {
			.media {
				border-top: 1px solid $dark-card-border;
			}
		}
		
		.modal-header {
			border-bottom: 1px solid $dark-card-border;
		}
		
		#right-history {
			background-color: $dark-card-background;
			box-shadow: 0 0 9px $dark-body-background;
			h6 {
				span {
					a {
						color: $dark-all-font-color;
					}
				}
			}
		}
		
		.ProfileCard {
			&:hover {
				color: $dark-all-font-color;
				background-color: $dark-card-background;
			}
		}
		
		.translate_wrapper.active .more_lang {
			&:before {
				border-bottom: 7px solid $dark-card-background;
			}
			
			.lang {
				border-bottom: 1px solid $dark-body-background;
			}
		}
		
		.toggle-sidebar {
			svg {
				stroke: var(--theme-deafult);
			}
		}
		
		.page-wrapper {
			.page-body-wrapper {
				background-color: $dark-body-background;
			}
			
			.sidebar-main-title {
				p {
					color: $dark-all-font-color;
				}
			}
			
			&.compact-wrapper {
				.page-body-wrapper {
					div.sidebar-wrapper {
						background-color: $dark-card-background;
						
						.sidebar-main {
							.sidebar-links {
								.sidebar-link.active {
									svg {
										stroke: var(--theme-deafult);
									}
								}
								
								li {
									a {
										span {
											color: $dark-all-font-color;
										}
										
										svg {
											stroke: $dark-all-font-color;
										}
									}
								}
								
								.simplebar-wrapper {
									.simplebar-mask {
										.simplebar-content-wrapper {
											.simplebar-content {
												>li {
													.sidebar-link {
														&.active {
															background-color: rgba($primary-color, 0.2);
															
															svg {
																color: var(--theme-deafult);
																stroke: var(--theme-deafult);
															}
														}
													}
													
													.mega-menu-container {
														.mega-box {
															.link-section {
																.submenu-title {
																	h5 {
																		color: $dark-all-font-color;
																	}
																}
																
																.submenu-content {
																	&.opensubmegamenu {
																		li {
																			a {
																				color: $dark-all-font-color;
																				font-weight: 400;
																			}
																		}
																	}
																}
															}
														}
														
														&::after {
															background-color: $light-all-font-color;
														}
													}
													
													.sidebar-submenu {
														li {
															a {
																color: $dark-all-font-color;
															}
														}
													}
												}
											}
										}
									}
								}
								.sidebar-list {
									ul.sidebar-submenu {
										li {
											a {
												span {
													color: $dark-all-font-color;
												}
												
												&.active {
													color: var(--theme-deafult);
												}
											}
										}
									}
								}
							}
						}
					}
					
					.according-menu {
						i {
							color: $dark-all-font-color;
						}
					}
				}
			}
			
			&.horizontal-wrapper {
				.page-body-wrapper {
					.sidebar-wrapper {
						background: $dark-card-background;
						border-top: 1px solid $dark-card-border;
						
						.sidebar-main {
							.sidebar-links {
								.simplebar-wrapper {
									.simplebar-mask {
										.simplebar-content-wrapper {
											.simplebar-content {
												>li {
													>a {
														color: $dark-all-font-color;
													}
													
													.sidebar-submenu {
														background: $dark-card-background;
														
														li {
															a {
																color: $dark-all-font-color;
																
																&.active,
																&:hover {
																	color: var(--theme-deafult);
																}
															}
															
															.nav-sub-childmenu {
																background: $dark-card-background;
															}
														}
													}
													
													.mega-menu-container {
														background: $dark-card-background;
														
														.mega-box {
															.link-section {
																.submenu-content {
																	li {
																		a {
																			color: $dark-all-font-color;
																		}
																	}
																}
															}
														}
													}
												}
											}
										}
									}
								}
							}
							
							.left-arrow,
							.right-arrow {
								background-color: $dark-card-background;
								color: $dark-all-font-color;
							}
						}
					}
				}
			}
			
			&.compact-sidebar {
				.page-body-wrapper {
					div {
						&.sidebar-wrapper {
							>div {
								background: $dark-card-background;
							}
							
							.sidebar-main {
								.sidebar-links {
									li {
										.sidebar-title {
											border-bottom: 1px solid $dark-card-border;
										}
										a {
											span {
												color: $dark-all-font-color;
											}
											
											svg {
												stroke: $dark-all-font-color;
											}
										}
										
										.sidebar-submenu,
										.mega-menu-container {
											background-color: $dark-card-background;
											li {
												a {
													border-bottom: 1px solid $dark-card-border;
												}
											}
										}
									}
									
									.simplebar-wrapper {
										.simplebar-mask {
											.simplebar-content-wrapper {
												.simplebar-content {
													>li {
														.sidebar-link {
															&.active {
																background-color: rgba($primary-color, 0.2);
																
																span {
																	color: var(--theme-deafult);
																}
																
																svg {
																	color: var(--theme-deafult);
																	stroke: var(--theme-deafult);
																}
															}
														}
														
														.mega-menu-container {
															.mega-box {
																.link-section {
																	.submenu-title {
																		h5 {
																			color: $dark-all-font-color;
																		}
																	}
																	
																	.submenu-content {
																		&.opensubmegamenu {
																			li {
																				a {
																					color: $dark-all-font-color;
																					font-weight: 400;
																				}
																			}
																		}
																	}
																}
															}
															
															&::after {
																background-color: $light-all-font-color;
															}
														}
														
														.sidebar-submenu {
															li {
																a {
																	color: $dark-all-font-color;
																}
															}
														}
													}
												}
											}
										}
									}
									
									.sidebar-list {
										ul.sidebar-submenu {
											li {
												a {
													span {
														color: $dark-all-font-color;
													}
													
													&.active {
														color: var(--theme-deafult);
													}
												}
											}
										}
									}
								}
							}
						}
					}
					
					.according-menu {
						i {
							color: $dark-all-font-color;
						}
					}
				}
			}
			
			&.material-type {
				.page-header {
					.header-wrapper {
						border-radius: 15px 15px 0 0;
					}
				}
			}
			
			// material icon css
			&.material-icon {
				.page-header {
					.header-wrapper {
						background-color: $transparent-color;
						
						.nav-right {
							
							.language-nav {
								.more_lang {
									color: $white;
								}
							}
							
							.onhover-show-div {
								color: $white;
							}
							
							.flip-card {
								.flip-card-inner {
									.back {
										.flip-back-content {
											input {
												border: 1px solid $dark-border-color;
												background: $dark-body-background;
												color: $white;
											}
										}
									}
								}
							}
							
						}
						
					}
					
					.header-logo-wrapper {
						background-color: $transparent-color;
					}
				}
				
				.page-body-wrapper {
					.page-body {
						background-color: $transparent-color;
					}
					
				}
			}
			
			&.advance-layout {
				background-color: $dark-body-background;
				.page-header {
					.header-wrapper {
						background: $transparent-color;
						
						.header-logo-wrapper {
							background: $transparent-color;
						}
					}
				}
				
				.page-body-wrapper {
					background: $transparent-color;
					
					.page-body {
						background: $transparent-color;
					}
				}
			}
			
			.page-header {
				.header-wrapper {
					.search-full {
						background-color: $dark-card-background;
						
						input {
							color: $dark-all-font-color;
						}
					}
				}
			}
		}
		
		::-webkit-calendar-picker-indicator {
			filter: invert(1);
		}
		
		.left-header {
			.mega-menu-container {
				background-color: $dark-card-background;
				border-top: 1px solid $dark-card-background;
				.mega-box {
					+.mega-box {
						border-left: 1px solid $dark-card-border;
					}
				}
			}
			
			.mega-menu div>div a {
				color: rgba($white, 0.7);
			}
			.level-menu {
				.header-level-menu {
					background-color: $dark-card-background;
					>li {
						.header-level-sub-menu {
							background-color: $dark-card-background;
						}
						a {
							color: rgba($white, 0.7);
						}
						svg {
							stroke: rgba($white, 0.7);
						}
					}
				}
			}
		}
		
		.kanban-board-header {
			background-color: $dark-body-background !important;
			border-bottom: 1px solid $dark-card-background !important;
		}
		
		.kanban-board .kanban-drag {
			background: $dark-body-background !important;
		}
		
		.kanban-container {
			.kanban-item {
				.kanban-box {
					border: 1px solid rgba($primary-color, 0.15);
					background-color: $dark-body-background;
				}
			}
		}
		
		.project-box {
			border: 1px solid rgba($primary-color, 0.15);
			background-color: $dark-body-background;
		}
		
		.file-content {
			.form-inline {
				border: 1px solid $dark-body-background;
			}
			
			.files {
				.file-box {
					border: 1px solid rgba($primary-color, 0.15);
					background-color: $dark-body-background;
					
					.file-top {
						background-color: $dark-card-background;
						border: 1px solid rgba($primary-color, 0.15);
					}
				}
			}
			
			.folder .folder-box {
				border: 1px solid rgba($primary-color, 0.15);
				background-color: $dark-body-background;
			}
		}
		
		.file-sidebar {
			.pricing-plan {
				border: 1px solid rgba($primary-color, 0.15) !important;
			}
			
			.btn-light {
				color: $dark-all-font-color;
				background-color: $dark-body-background !important;
				border: 1px solid rgba($primary-color, 0.15) !important;
			}
		}
		
		#lnb {
			border-right: 1px solid $dark-card-border;
		}
		
		.lnb-new-schedule,
		.lnb-calendars>div {
			border-bottom: 1px solid $dark-card-border;
		}
		
		#menu .sidebar-list .btn-default {
			color: rgba($dark-all-font-color, 0.2);
		}
		
		.tui-full-calendar-timegrid-hour {
			background-color: $dark-card-background;
			color: $white !important;
		}
		
		.tui-full-calendar-timegrid-gridline {
			border-bottom: 1px solid $dark-card-border !important;
		}
		
		.tui-full-calendar-time-date,
		.tui-full-calendar-weekday-grid-line,
		.tui-full-calendar-left,
		.tui-full-calendar-timegrid-timezone {
			border-right-color: $dark-card-border !important;
		}
		
		.tui-full-calendar-popup {
			color: $theme-body-font-color;
		}
		
		#menu {
			.btn-default {
				color: $dark-all-font-color;
				&:hover {
					color: $theme-body-font-color;
				}
			}
			.dropdown-menu {
				color: $dark-all-font-color;
			}
		}
		
		.tui-full-calendar-dayname-container,
		.tui-full-calendar-splitter {
			border-top-color: $dark-card-border !important;
			border-bottom-color: $dark-card-border !important;
		}
		
		span.tui-full-calendar-dayname-date-area {
			color: $white !important;
		}
		
		.tui-full-calendar-layout {
			background-color: $dark-card-background !important;
		}
		
		.chat-default {
			.card-body {
				&.chat-box {
					.right-side-chat {
						.media-body {
							.message-main {
								span {
									background-color: var(--theme-deafult);
									color: $dark-all-font-color;
									
									&.loader-span {
										.dot {
											background: $dark-all-font-color;
										}
									}
								}
							}
						}
					}
				}
			}
		}
		
		// index page css end	
		.blockquote {
			border-left: 4px solid $dark-card-border;
		}
		.figure {
			&.text-end {
				blockquote {
					border-right-color: $dark-card-border;
				}
