		$alert-name:primary,
		secondary,
		success,
		danger,
		warning,
		info,
		light,
		dark;
		$alert-color : $primary-color,
		$secondary-color,
		$success-color,
		$danger-color,
		$warning-color,
		$info-color,
		$light-color,
		$dark-color;
		
		@each $var in $alert-name {
			$i: index($alert-name, $var);
			
			.alert-#{$var} {
				background-color: rgba(nth($alert-color, $i), 0.8) !important;
				border-color: rgba(nth($alert-color, $i), 0.9) !important;
				color: $white;
				
				.progress {
					height: 5px;
					background-color: darken(nth($alert-color, $i), 1%);
					border-radius: 0;
				}
				
				.progress-bar {
					background-color: lighten(nth($alert-color, $i), 50%);
				}
			}
			
			.alert-#{$var}.inverse {
				background-color: $transparent-color !important;
				.btn-close {
					filter: brightness(1) invert(1);
				}
			}
			.alert-#{$var}.outline,
			.alert-#{$var}.outline-2x {
				.btn-close {
					filter: brightness(1) invert(1);
				}
			}
			.dismiss-text {
				.alert-dismissible {
					.btn-close {
						filter: unset;
						
						.bg-light {
							color: $theme-body-font-color !important;
						}
					}
				}
			}
		}
		
