            stroke: $white;
          }
          >a{
            > svg{
              stroke: $white;
            }
          }
          >div,>span{
            .lang-txt{
              color: $white;
            }
            i{
              color: $white;
            }
             svg{
              stroke: $white;
            }
          }
        }
